<template>

<p>currently this video has {{ likes }} likes</p>
<p v-if="likes > 0">YAYY YOU GOT MORE THAN 0 LIKES</p>

<p v-if="likes <= 0">Oh no you got {{ likes }} :(</p>

<button v-on:click="like()">
    Click me
</button>

<br>

<button v-on:click="dislike()">
    Dont click me
</button>

</template>

<script>
export default {
    data() {
        return {
            likes: 0,
        }
    },
    methods: {
        like() {
            this.likes+=1
        },

        dislike() {
            this.likes-=1
        }
    }
}
</script>